'use client'

import { useState, useEffect, Suspense } from 'react'
import { useSearchParams, useRouter } from 'next/navigation'
import Image from 'next/image'

/**
 * Demo Access Page
 * 
 * Uses custom demo login flow instead of next-auth signIn
 * This avoids issues with SessionProvider and custom JWT tokens
 */

function DemoAccessContent() {
  const searchParams = useSearchParams()
  const router = useRouter()
  const token = searchParams.get('token')
  
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [needsAccessCode, setNeedsAccessCode] = useState(false)
  const [accessCode, setAccessCode] = useState('')
  const [demoInfo, setDemoInfo] = useState<{ name: string; expiresAt: string } | null>(null)

  useEffect(() => {
    if (!token) {
      setError('No demo access token provided')
      setLoading(false)
      return
    }

    // Validate the token first
    validateToken()
  }, [token])

  const validateToken = async () => {
    try {
      const response = await fetch('/api/demo/validate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ token }),
      })

      const data = await response.json()

      if (!response.ok || !data.valid) {
        setError(data.error || 'Invalid or expired demo access')
        setLoading(false)
        return
      }

      setDemoInfo({
        name: data.name,
        expiresAt: new Date(data.expiresAt).toLocaleDateString(),
      })

      if (data.requiresAccessCode) {
        setNeedsAccessCode(true)
        setLoading(false)
      } else {
        // Auto-login if no access code required
        await handleDemoLogin()
      }
    } catch (err) {
      setError('Failed to validate demo access')
      setLoading(false)
    }
  }

  const handleDemoLogin = async (code?: string) => {
    setLoading(true)
    setError('')

    try {
      // Use custom demo login endpoint instead of next-auth signIn
      const response = await fetch('/api/demo/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          token,
          accessCode: code || '',
        }),
        credentials: 'include',
      })

      const data = await response.json()

      if (!response.ok || !data.success) {
        setError(data.error || 'Failed to activate demo access')
        setLoading(false)
      } else {
        // Redirect to demo dashboard
        router.push('/sub-account/dashboard?demo=true')
      }
    } catch (err) {
      setError('Failed to activate demo access')
      setLoading(false)
    }
  }

  const handleSubmitAccessCode = (e: React.FormEvent) => {
    e.preventDefault()
    if (accessCode.length !== 6) {
      setError('Please enter the 6-digit access code')
      return
    }
    handleDemoLogin(accessCode)
  }

  // Loading state
  if (loading && !needsAccessCode) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100">
        <div className="text-center">
          <div className="inline-flex items-center justify-center mb-6">
            <div className="w-16 h-16 border-4 border-blue-600 border-t-transparent rounded-full animate-spin"></div>
          </div>
          <h2 className="text-2xl font-semibold text-gray-900 mb-2">
            Preparing Your Demo Environment
          </h2>
          <p className="text-gray-600">
            Setting up sample data and configuring your demo access...
          </p>
        </div>
      </div>
    )
  }

  // Error state (no token)
  if (error && !token) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100">
        <div className="max-w-md w-full mx-4">
          <div className="bg-white rounded-2xl shadow-xl p-8 text-center">
            <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-6">
              <svg className="w-8 h-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
              </svg>
            </div>
            <h2 className="text-2xl font-bold text-gray-900 mb-2">
              Invalid Demo Link
            </h2>
            <p className="text-gray-600 mb-6">
              This demo access link is invalid. Please check your email for the correct link or contact us for a new one.
            </p>
            <a
              href="https://risivo.com/contact"
              className="inline-flex items-center justify-center px-6 py-3 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 transition-colors"
            >
              Request Demo Access
            </a>
          </div>
        </div>
      </div>
    )
  }

  // Access code form
  if (needsAccessCode) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100">
        <div className="max-w-md w-full mx-4">
          <div className="bg-white rounded-2xl shadow-xl p-8">
            {/* Logo */}
            <div className="text-center mb-8">
              <Image
                src="/risivo-logo-color.png"
                alt="RISIVO CRM"
                width={180}
                height={41}
                className="mx-auto mb-4"
              />
              <h2 className="text-2xl font-bold text-gray-900">
                Welcome, {demoInfo?.name}!
              </h2>
              <p className="text-gray-600 mt-2">
                Enter your access code to continue
              </p>
            </div>

            {/* Error Message */}
            {error && (
              <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
                <p className="text-sm text-red-600">{error}</p>
              </div>
            )}

            {/* Access Code Form */}
            <form onSubmit={handleSubmitAccessCode} className="space-y-6">
              <div>
                <label htmlFor="accessCode" className="block text-sm font-medium text-gray-700 mb-2">
                  6-Digit Access Code
                </label>
                <input
                  id="accessCode"
                  type="text"
                  inputMode="numeric"
                  pattern="[0-9]*"
                  maxLength={6}
                  value={accessCode}
                  onChange={(e) => setAccessCode(e.target.value.replace(/\D/g, ''))}
                  className="w-full px-4 py-3 text-center text-2xl tracking-widest border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="• • • • • •"
                  autoFocus
                />
                <p className="mt-2 text-sm text-gray-500">
                  The access code was sent to your email
                </p>
              </div>

              <button
                type="submit"
                disabled={loading || accessCode.length !== 6}
                className="w-full py-3 px-4 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? (
                  <span className="flex items-center justify-center">
                    <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" fill="none" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                    </svg>
                    Verifying...
                  </span>
                ) : (
                  'Access Demo'
                )}
              </button>
            </form>

            {/* Demo Info */}
            <div className="mt-6 pt-6 border-t border-gray-200">
              <div className="flex items-center justify-between text-sm text-gray-500">
                <span>Demo expires</span>
                <span className="font-medium">{demoInfo?.expiresAt}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

  // Generic error state
  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100">
      <div className="max-w-md w-full mx-4">
        <div className="bg-white rounded-2xl shadow-xl p-8 text-center">
          <div className="w-16 h-16 bg-yellow-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <svg className="w-8 h-8 text-yellow-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">
            {error || 'Something went wrong'}
          </h2>
          <p className="text-gray-600 mb-6">
            We couldn't process your demo access. Please try again or request a new demo link.
          </p>
          <div className="space-y-3">
            <button
              onClick={() => window.location.reload()}
              className="w-full py-3 px-4 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 transition-colors"
            >
              Try Again
            </button>
            <a
              href="https://risivo.com/contact"
              className="block w-full py-3 px-4 border border-gray-300 text-gray-700 font-medium rounded-lg hover:bg-gray-50 transition-colors text-center"
            >
              Contact Support
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}

export default function DemoAccessPage() {
  return (
    <Suspense fallback={
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100">
        <div className="text-center">
          <div className="inline-flex items-center justify-center mb-6">
            <div className="w-16 h-16 border-4 border-blue-600 border-t-transparent rounded-full animate-spin"></div>
          </div>
          <h2 className="text-2xl font-semibold text-gray-900 mb-2">Loading...</h2>
        </div>
      </div>
    }>
      <DemoAccessContent />
    </Suspense>
  )
}
